<template>
  <div class="container">
    <b-card title="Pengaturan">
      <b-row class="container my-3">
        <b-button 
          size="md" 
          @click="refresh"
          variant="dark"
        >
          Perbaharui 
        </b-button>
      </b-row>
      <b-form class="container my-2" @submit="on_submit">
        <b-form-group 
          class="container" id="input1" 
          v-for="set in setting"
          :key="set.material"
        > 
          <b-row >
            <b-col >
              <p class="mt-2">Material : {{set.material}}</p> 
            </b-col>
            <b-col>
              <b-row>
                <b-col >
                  <p class="mt-1"> Toleransi (Kg) :</p> 
                </b-col>
                <b-col >
                  <b-form-input 
                    type="text" 
                    :id="create_id(set.material, 'tol')" 
                    v-model="set.tolerance" 
                    :state="tol_check(set.tolerance)"
                    aria-describedby="tolerance_help"
                    :placeholder="set.tolerance.toString()" 
                    :disabled="disabled_state"
                  >
                  </b-form-input>
                  <b-form-invalid-feedback id="tolerance_help">
                    {{tol_help}}
                  </b-form-invalid-feedback>
                </b-col>
              </b-row>      
            </b-col>
            <b-col>
              <b-row>
                <b-col >
                  <p class="mt-1"> Timbangan :</p> 
                </b-col>
                <b-col >
                  <b-form-input 
                    type="text" 
                    :id="create_id(set.material, 'stat')" 
                    v-model="set.station" 
                    :state="station_check(set.station)"
                    aria-describedby="stat_help"
                    :placeholder="set.station.toString()" 
                    :disabled="disabled_state"
                  >
                  </b-form-input>
                  <b-form-invalid-feedback id="stat_help">
                    {{stat_help}}
                  </b-form-invalid-feedback>
                </b-col>
              </b-row>
            </b-col>
          </b-row>    
        </b-form-group>
        <b-button class="m-3 " v-if="setting_loaded" @click="disabled_state=false" type="button" variant="secondary">Ubah Pengaturan</b-button>
        <b-button class="m-3 " v-if="setting_loaded" type="submit" variant="primary">Simpan</b-button>   
      </b-form>    
    </b-card> 
  </div>
</template>

<script> 
import axios from "axios";

export default { 
  name: "Setting",
  data(){
    return{
      setting:[],
      setting_loaded:false,
      tol_help:'',
      stat_help:'',
      login_alert:false,
      disabled_state:true,
      alert_string:''
    }
  },
  components: {
  },
  methods: {
    refresh(){
      axios
      .get(`/api/setting`, {
        timeout: 5000,
      })
      .then((res) => {
        if (res.data.length > 0) {
          this.setting = res.data;
          this.setting_loaded = true;
        } 
      })
      .catch((err) => {
        this.setting_loaded = false
        console.error(err);
      });
    },
    create_id(material,str){
      return `${material}_${str}`
    },
    tol_check(tol){
      if (tol <= 0 || tol == null){
        this.tol_help = "Nilai Salah";
        return false
      }
      else{
        return true
      }
    },
    station_check(station){
      if (station <= 0 || station > 8 || station == null){
        this.stat_help = "Nilai Salah";
        return false
      }
      else{
        return true
      }
    },
    on_submit(event){
      event.preventDefault();
      axios
      .put(`/api/setting`, {
        timeout: 5000,
        data:this.setting
      })
      .then(() => {
        this.disabled_state = true;
      })
      .catch((err) => {
        console.log(err)        
      });
    }
  }
};
</script>
